body {
  font-family: Helvetica, Arial, sans-serif;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.app-body{
  display:-ms-flexbox;
  display:flex;
}

.word-view {
  -ms-flex:2 1;
      flex:2 1;
}

.answer-view {
  -ms-flex:2 1;
      flex:2 1;
}

.textarea {
  width:100%;
  height:40vh;
}

.full-screen {
  height:100vh;
  width:100vw;
  background:#55d27b;
  display:-ms-flexbox;
  display:flex;
  position:absolute;
  top:0;
  left:0;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align:center;
      align-items:center;
  color:#fff;
}

textarea {
  border:0;
  width:75%;
  height:75%;
  background:transparent;
  color:#fff;
  font-size:3em;
  border: none;
  overflow: auto;
  outline: none;
  -webkit-box-shadow: none;
  box-shadow: none;
}

.large-text {
  font-size:5em;
  font-weight: 600;
}

.full-screen div {
  width:500px;
}



